@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  background-color: #fff;
}

body {
  font-family: "Poppins", sans-serif;
}

.container {
  margin: 10px auto;
  overflow: auto;
  min-height: 300px;
  padding: 10px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 500px;
  padding-bottom: 10px;
  margin: 10px auto;
  overflow: auto;
}

.btn {
  display: inline-block;
  background: #000;
  color: #fff;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
}

.btn:focus {
  outline: none;
}

.btn:active {
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}

.btn-block {
  display: block;
  width: 100%;
}
.add-form {
  align-items: center;
  max-width: 500px;
  padding-bottom: 10px;
  margin: 20px auto;
  overflow: auto;
}

.add-form input {
  margin-top: 30px;
}

.form-control {
  margin: 20px 0;
}
.form-control label {
  display: block;
}

.form-control input[type="text"],
.form-control input[type="number"],
.form-control select,
.form-control textarea {
  width: 80%;
  height: 40px;
  margin: 5px;
  padding: 3px 7px;
  font-size: 17px;
  border-style: solid;
}
.form-control message {
  width: 80%;
  height: 40px;
  margin: 5px;
  padding: 3px 7px;
  font-size: 17px;
}

.form-control-check {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.form-control-check label {
  flex: 1 1;
}
.form-control-check input {
  flex: 2 1;
  height: 20px;
}

table {
  border-collapse: collapse;
  width: 100%;
}

th,
td {
  border: 1px solid #ffffff;
  text-align: left;
  padding: 8px;
  font-size: 15px;
}

th {
  background-color: #333;
  color: #fff;
}

td {
  background-color: #888;
}

.table th {
  /* background: #fff; */
  padding: 8px 10px;
  border-bottom: 1px solid #1a1a1a;
  font-weight: bold;
  text-align: left;
}

